import "./fetchIncident.css";

import { useForm } from "react-hook-form";
import React, { useState } from "react";
import ReactJson from "react-json-view";
import { getRequest } from "../../service/service";

export const FetchIncidents = () => {
  const { register, handleSubmit } = useForm();
  const onSubmit = async (data) => {
    //make request to server
    const res = await getRequest("/fetchIncidents", data);
    setResult(res);
  };
  const [result, setResult] = useState(null);

  const styleJsonViewer = {
    width: "100%",
    height: "100%",
    overflowY: "auto",
    alignSelf: "center",
  };

  return (
    <div className="fetch-incidents">
      <form className="filter-form" onSubmit={handleSubmit(onSubmit)}>
        <p className="filter--text">Filters{"[Optional]"}</p>
        <div className="filter--bar">
          <input
            className="form--input"
            placeholder="city"
            {...register("city")}
          />
        </div>
        <div className="filter--bar-split">
          <input
            className="form--input-split"
            placeholder="humidity min"
            {...register("humidity_range.min")}
            type="number"
          />
          <input
            className="form--input-split"
            placeholder="humidity max"
            {...register("humidity_range.max")}
            type="number"
          />
        </div>
        <div className="filter--bar-split">
          <input
            className="form--input-split"
            placeholder="temp min"
            {...register("temp_range.min")}
            type="number"
          />
          <input
            className="form--input-split"
            placeholder="temp max"
            {...register("temp_range.max")}
            type="number"
          />
        </div>
        <button className="form--button">Submit</button>
      </form>
      <div className="result-box">
        {result ? (
          <ReactJson src={result} style={styleJsonViewer} />
        ) : (
          <p className="result--empty">No result yet..</p>
        )}
      </div>
    </div>
  );
};
