import axios from "axios";

const host = "https://enyatair.fly.dev";

export const postRequest = async (path, data) => {
  try {
    const url = host + path;

    const result = await axios.post(url, data);

    return result.data;
  } catch (err) {
    const d = err.response;
    return d.data;
  }
};

export const getRequest = async (path, data) => {
  try {
    const url = host + path;

    const result = await axios.get(url, { params: data });
    return result.data;
  } catch (err) {
    const d = err.response;
    return d.data;
  }
};

export const parsestringtoInt = (str) => {
  if (str === "") {
    return 0;
  }

  return parseInt(str);
};

export const parseIncident = (d) => {
  const data = d;
  data.humidity_range.max = parsestringtoInt(data.humidity_range.max);
  data.humidity_range.min = parsestringtoInt(data.humidity_range.min);
  data.temp_range.max = parsestringtoInt(data.temp_range.max);
  data.temp_range.min = parsestringtoInt(data.temp_range.min);

  return data;
};
