import "./incidentform.css";

import { useForm } from "react-hook-form";
import ReactJson from "react-json-view";
import React, { useState } from "react";
import { postRequest } from "../../service/service";

export const CreateIncident = () => {
  const { register, handleSubmit } = useForm();
  const onSubmit = async (data) => {
    //make request to server
    const res = await postRequest("/createIncident", data);
    setResult(res);
  };
  const [result, setResult] = useState(null);

  const styleJsonViewer = {
    width: "100%",
    height: "100%",
    overflowY: "auto",
    alignSelf: "center",
  };

  return (
    <div className="create-incident">
      <form className="ci-form" onSubmit={handleSubmit(onSubmit)}>
        <p className="filter--text">
          Incident Form, All fields required for valid report
        </p>

        <div className="ci--bar">
          <input
            className="form--input"
            placeholder="client_id"
            {...register("client_id")}
            type="number"
          />
        </div>
        <div className="ci--bar">
          <input
            className="form--input"
            placeholder="incident description"
            {...register("incident_desc")}
          />
        </div>
        <div className="ci--bar">
          <input
            className="form--input"
            placeholder="city"
            {...register("city")}
          />
        </div>

        <div className="ci--bar">
          <input
            className="form--input"
            placeholder="country"
            {...register("country")}
          />
        </div>
        <button className="form--button">Submit</button>
      </form>
      <div className="result-box--if">
        {result ? (
          <ReactJson src={result} style={styleJsonViewer} />
        ) : (
          <p className="result--empty">No result yet..</p>
        )}
      </div>
    </div>
  );
};
