import "./incidentCountry.css";
import { useForm } from "react-hook-form";
import React, { useState } from "react";
import ReactJson from "react-json-view";
import { postRequest } from "../../service/service";

export const IncidentByCountry = () => {
  const { register, handleSubmit } = useForm();
  const onSubmit = async (data) => {
    //make request to server
    const res = await postRequest("/incidentByCountry", data);
    setResult(res);
  };
  const [result, setResult] = useState(null);
  const styleJsonViewer = {
    width: "100%",
    height: "100%",
    overflowY: "auto",
    alignSelf: "center",
  };
  return (
    <div className="incident-country">
      <form className="filter-form--ic" onSubmit={handleSubmit(onSubmit)}>
        <p className="filter--text">Filters reports by country{"[required]"}</p>
        <div className="filter--bar">
          <input
            className="form--input"
            placeholder="country"
            {...register("country")}
          />
        </div>
        <button className="form--button">Submit</button>
      </form>
      <div className="result-box--ic">
        {result ? (
          <ReactJson src={result} style={styleJsonViewer} />
        ) : (
          <p className="result--empty">No result yet..</p>
        )}
      </div>
    </div>
  );
};
